import * as util from './util.js'

export function R_alert(config) {
	if (typeof config === "string") {
		config = {
			content: config
		}
	}
	const {
		title = "", content = "", showCancel = false, cancelText = "", confirmText = "", success = () => {}
	} = config
	uni.showModal({
		title,
		content,
		success,
		showCancel,
		cancelText,
		confirmText,
		confirmColor: "#EA5503"
	})
}

export function R_confirm(config) {
	const {
		title = "", content = "", success = () => {}, fail = () => {}
	} = config
	uni.showModal({
		title,
		content,
		success,
		fail,
		confirmColor: "#EA5503"
	})
}

/**
 * 
 */
export const showToast = msg => {
	uni.showToast({
		title: msg,
		icon: 'none'
	})
}

