/**
 * 
 * @param {Object} resList 
 * @param {Object} oldList 
 * @param {int} pageNo 
 */
export const getMoreListData = (resList, oldList, pageNo) => {
	// 
	if (pageNo == 1) oldList.data = []
	// 
	return oldList.data.concat(resList.data)
}

/**
 * tabBar ()
 * tabBarLinks, 
 */
export const getTabBarLinks = () => {
	const tabBarLinks = [
		'pages/index/index',
		'pages/my/my',
		'pages/shopCard/shopCard',
		'pages/category/category'
	]
	return tabBarLinks
}

/**
 * url
 * tabBar
 * @param {string}  url
 * @param {object}  query
 */
export const navTo = (url, query = {}) => {
	if (!url || url.length == 0) {
		return false
	}
	console.log(util)
	// tabBar, switchTab
	if (util.inArray(url, getTabBarLinks())) {
		uni.switchTab({
			url: `/${url}`
		})
		return true
	}
	// query
	const queryStr = !util.isEmpty(query) ? '?' + util.urlEncode(query) : ''
	// , navigateTo
	uni.navigateTo({
		url: `/${url}${queryStr}`
	})
	return true
}
